*{
  margin: 0;
  padding: 0;
}
.container{
  background: linear-gradient(to right bottom ,hsl(30, 92%, 51%), #e38226 );
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.app-wrapper{
  background-color: #1d0065;
  min-width: 230px;
  min-height: 580px;
  padding: 30px;
  box-sizing: border-box;
  border-radius: 10px;
  box-shadow: 3px 6px 40px #000;
}
.header h1{
  color: #fff;
  text-align: center;
  margin: 30px 0;
}

.task-input{
  outline: none;
  width: 260px;
  padding: 15px;
  margin-right: 25px;
  font-size: 20px;
  color: #ccc;
  background-color: #000000;
  border: 1px solid #c89666;
  border-radius: 10px;
}
.button-add{
  width: 70px;
  padding: 15px 15px;
  font-size: 20px;
  border-radius: 10px;
  border: 0;
  background-color: #f1af71;
  cursor: pointer;
}

.list-item{
  display: flex;
  margin:  20px 0;
  border: 1px solid #ccc;
  border-radius: 10px;
  padding: 10px;
  max-height: 30px;
}

.button-complete,
.button-delete,
.button-edit{
  border: none;
  outline: none;
  cursor: pointer;
  background-color: transparent;
  font-size: 28px;
  border-radius: 50%;
}

.button-complete{
  color: #f1af71;
  margin-right:  10px;

}
.button-edit{
  color: #e38226;
  margin-right: 10px;
}
.button-delete{
  color: aqua;

}

.button-complete{
  text-decoration-style:solid ;
  text-decoration-line:line-through;
  text-decoration-color:#045af0 ;
}

.list{
  width: 220px;
  background-color: #020202;
  border: none;
  color: rgb(249, 249, 255);
  font-size: 20px;
  padding-left: 10px;
  margin-right: 15px;
}

input:focus,
button:focus {
  outline: none;
}